<template>
  <q-page padding class="flex flex-center">
    <AuthenticationForm @success="loginSuccess" @failed="loginFailed" />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import AuthenticationForm from 'src/components/Forms/Form_Authentication.vue';

export default defineComponent({
  name: 'AuthenticationPage',

  data() {
    return {
      title: 'Authentication',
    };
  },

  components: { AuthenticationForm },

  async mounted() {
    this.$root.setMeta({ title: this.title });
  },

  methods: {
    loginSuccess(res) {
      // Success
      this.redirectOnLogin();
      this.$q.notify({
        icon: 'icon-check-circle-broken',
        message: 'Login Success',
        timeout: 500,
      });
    },

    loginFailed(res) {
      // Failed
      this.$q.notify({
        message: 'Login Failed',
        icon: 'icon-alert-circle',
        iconColor: 'negative',
        timeout: 1000,
      });
    },

    redirectOnLogin() {
      this.$router.push(this.$route.query.redirect || '/');
    },
  },
});
</script>
