<template>
  <q-card class="rounded shadows-md" style="width: 560px; max-width: 90vw">
    <q-toolbar class="bg-primary text-white padding-3y padding-6x">
      <GrowmodoTextIcon size="32px" :text="false" :dark="true" />
      <q-toolbar-title>
        <h1 class="text-h5 font-medium q-ma-none">
          Login to {{ $config?.pkjson?.productName || 'Growmodo Hub' }}
        </h1>
      </q-toolbar-title>
    </q-toolbar>
    <q-form
      @submit="loginWithPwd"
      ref="authorization_form"
      class="card-padding"
    >
      <div class="row q-col-gutter-lg">
        <div class="col-12">
          <q-input
            outlined
            autofocus
            clearable
            lazy-rules
            autocomplete
            hide-bottom-space
            tabindex="1"
            clear-icon="icon-x-close"
            placeholder="Username or email"
            v-model="u_useridentity"
            :input-class="$config?.class?.activeInput"
            :rules="[(val) => checker.input(!!val || '', 250)]"
            @keyup="modKey"
            class="rounded input-lg format-bold"
          >
            <template v-slot:prepend>
              <q-icon
                size="sm"
                name="icon-user-03"
                class="hover-text-secondary"
              />
            </template>
          </q-input>
        </div>
        <div class="col-12">
          <InputPassword
            :dense="false"
            withicon
            outlined
            clearable
            lazy-rules
            autocomplete
            hide-bottom-space
            tabindex="2"
            placeholder="Password"
            clear-icon="icon-x-close"
            v-model="u_password"
            :input-class="$config?.class?.activeInput"
            :rules="[(val) => checker.input(!!val || '', 250)]"
            @keyup="modKey"
            class="rounded input-lg format-bold"
          />
        </div>

        <!-- action button -->
        <div class="col-12">
          <div class="row q-col-gutter-md justify-end">
            <div :class="{ ['full-width']: $q.screen.lt.sm }">
              <q-btn
                outline
                no-caps
                unelevated
                tabindex="3"
                :ripple="{ color: 'accent' }"
                color="primary"
                class="rounded button-gm-outline-dark button-lg text-body1 font-medium q-px-lg"
                :class="{ ['full-width']: $q.screen.lt.sm }"
                to="/signup"
                :disable="decrypting.status"
              >
                Sign Up
              </q-btn>
            </div>
            <div :class="{ ['full-width']: $q.screen.lt.sm }">
              <q-btn
                no-caps
                unelevated
                tabindex="3"
                :ripple="{ color: 'accent' }"
                color="primary"
                class="rounded slide-hover button-lg text-body1 font-medium q-px-lg"
                :class="{ ['full-width']: $q.screen.lt.sm }"
                :disable="decrypting.status"
                :percentage="decrypting.percentage"
                @click="submitValidation"
              >
                Log In
                <q-icon
                  size="20px"
                  name="icon-arrow-narrow-right"
                  class="q-pl-xs"
                />

                <template v-slot:loading>
                  <q-spinner
                    size="sm"
                    class="on-left"
                    v-if="decrypting.status"
                  />
                  Verifying
                </template>
              </q-btn>
            </div>
          </div>
        </div>
      </div>
    </q-form>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import {
  defaultModKey,
  checker,
  tryClearInterval,
} from 'src/assets/scripts/functions';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import { mapState, mapActions } from 'pinia';
import useUser from 'stores/user';

export default defineComponent({
  name: 'AuthenticationForm',

  components: { GrowmodoTextIcon, InputPassword },

  props: {
    allowSignup: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      checker,
      loginAnimate: null,
      u_useridentity: null,
      u_password: null,
      interval: null,
      login_response: null,
      decrypting: { status: false, percentage: 0 },
    };
  },

  computed: {
    ...mapState(useUser, [
      'selectedOrgAccountPending',
      'selectedOrgIsCancelled',
    ]),
  },

  watch: {
    login_response: {
      async handler(val) {
        if (val) {
          tryClearInterval(this.loginAnimate);
          setTimeout(() => {
            this.decrypting.status = false;
          }, 500);
        }
      },
    },
  },

  emits: [
    'verify_email',
    'success',
    'failed',
    'loading',
    'pending',
    'onSignup',
    'cancelled',
    'expired',
  ],

  methods: {
    ...mapActions(useUser, [
      'loginWithPassword',
      'isEmailVerified',
      'onSignupStep',
    ]),

    submitValidation() {
      this.$refs?.authorization_form?.submit?.();
    },

    async loginWithPwd() {
      this.loginLoading();
      this.login_response = null;
      const data = {
        username: this.u_useridentity,
        password: this.u_password,
      };
      this.$emit('loading', data);
      // Send Request to API
      const response = await this.loginWithPassword(data);
      this.login_response = response || {};
      // Emit Login Response
      const email_verified = this.isEmailVerified(this.login_response);
      const onSignup = this.onSignupStep(
        this.login_response.data?.user?.signup_step
      );
      if (this.login_response?.success) {
        if (this.selectedOrgIsCancelled) {
          this.$emit('cancelled', this.login_response?.data);
        } else if (this.$root.selectedOrgIsExpired) {
          this.$emit('expired', this.login_response?.data);
        } else if (this.selectedOrgAccountPending) {
          this.$emit('pending', this.login_response?.data);
        } else if (!email_verified) {
          this.$emit('verify_email', this.login_response?.data?.user);
        } else if (onSignup) {
          this.$emit('onSignup', onSignup);
        } else {
          this.$emit('success', this.login_response || {});
        }
      } else {
        this.$emit('failed', this.login_response || {});
      }
    },

    async loginLoading(callback) {
      this.decrypting.status = true;
      this.loginAnimate = setInterval(() => {
        this.decrypting.percentage += Math.floor(Math.random() * 8 + 10);
        if (this.decrypting.percentage >= 100) {
          if (callback) callback();
          this.decrypting.status = false;
          tryClearInterval(this.loginAnimate);
        }
      }, 1000);
    },

    /**
     * Modify Enter Key for Inputs
     * @param {KeyboardEvent} evt event object
     */
    async modKey(evt) {
      defaultModKey(evt, this.submitValidation);
    },
  },
});
</script>
